import { createContext, useContext, useReducer } from "react";
import { appDataReducer } from "../reducer";
const AppDataContext = createContext();

const AppDataContextProvider = ({ children }) => {
  const appInitialState = {
    productsList: [],
    filterData: []
  };

  const [appDataState, appDataDispatch] = useReducer(
    appDataReducer,
    appInitialState
  );
  return (
    <AppDataContext.Provider value={{ appDataState, appDataDispatch }}>
      {children}
    </AppDataContext.Provider>
  );
};

const useAppData = () => useContext(AppDataContext);

export { AppDataContextProvider, useAppData };
