export const Size = () => {
  const sizeList = [
    {
      id: "1",
      size: "S"
    },
    {
      id: "2",
      size: "M"
    },
    {
      id: "3",
      size: "L"
    },
    {
      id: "4",
      size: "XL"
    }
  ];

  return (
    <div className="filter__item">
      <div>Size:</div>
      <div className="size__wrapper">
        {sizeList.map((size) => {
          return <span key={size.id}>{size.size}</span>;
        })}
      </div>
    </div>
  );
};
