import { data } from "../../data/data";

export const Brands = () => {
  const brandList = [
    {
      id: "brand1",
      brand: "Levi's"
    },
    {
      id: "brand2",
      brand: "Van Heusen"
    },
    {
      id: "brand3",
      brand: "Wrangler"
    },
    {
      id: "brand4",
      brand: "Allen Solly"
    }
  ];

  return (
    <div className="filter__item">
      <div>Brands:</div>
      <div className="filter__content">
        {brandList.map((brand) => {
          return (
            <div key={brand.id}>
              <label htmlFor={brand.id}>
                <input type="checkbox" name={brand.brand} id={brand.id} />
                {brand.brand}
              </label>
            </div>
          );
        })}
      </div>
    </div>
  );
};
