import { useEffect } from "react";
import { Filter, ProductCard } from "../../components";
import { useAppData } from "../../context/AppDataContext";
import { data } from "../../data/data";
import "./products-listing.css";

export const ProductsListing = () => {
  console.log(data);
  const { appDataState, appDataDispatch } = useAppData();

  useEffect(() => {
    appDataDispatch({
      type: "INIT_PRODUCTS_DATA",
      payload: {
        productsData: data,
        filterData: data
      }
    });
  }, []);

  console.log(appDataState);
  return (
    <div className="products--listing">
      <div>
        <Filter />
      </div>
      <div className="products--listing__wrapper">
        {appDataState.filterData.length > 0 ? (
          appDataState.filterData.map((product) => {
            return <ProductCard data={product} key={product.id} />;
          })
        ) : (
          <div>Empty Products</div>
        )}
      </div>
    </div>
  );
};
